import React from 'react';
import Header from "./Header";
import Footer from "./Footer";
import axios from "axios";

class Subtitle extends React.Component {
    host = window.location.host.indexOf('localhost') >= 0 ? "http://localhost:9000" : "http://asr.flamingo-app.cn";

    constructor(props) {
        super(props);

        let fields = window.location.href.split("/");
        this.taskId = fields[fields.length - 1];

        this.state = {
            taskProgress: 0,
            taskStatus: 'READY',
            resultText: '',
            fileName: '',
        }

        this.startAsr = this.startAsr.bind(this);
    }

    status2ActionName(status) {
        switch (status) {
            case 'READY':
                return '';
            case 'RUNNING':
                return '...';
            case 'SUCCESS':
                return ''
            default:
                return 'Unknown'
        }
    }

    componentDidMount() {
        axios.get(this.host + '/asr/GetTask?taskId=' + this.taskId)
            .then(resp => {
                let task = resp.data.data;

                this.updateState(task);
            });
    }

    updateState(task) {
        this.setState({
            taskProgress: task.taskProgress,
            taskStatus: task.taskStatus,
            fileName: task.originalFileName
        });

        // 
        if (task.taskStatus === 'SUCCESS') {
            axios.get(this.host + '/asr/GetSrtFile?taskId=' + this.taskId)
                .then(resp => {
                    let text = resp.data.data;
                    this.setState({"resultText": text})
                });
        } else if (task.taskStatus === "RUNNING") {
            if (!this.interval) {
                this.refreshCnt = 0;
                this.maxRefreshCnt = 1200;
                this.interval = setInterval(() => {
                    // 
                    this.refreshCnt += 1;
                    if (this.refreshCnt > this.maxRefreshCnt) {
                        return;
                    }

                    axios.get(this.host + '/asr/GetTask?taskId=' + this.taskId)
                        .then(resp => {
                            let task = resp.data.data;
                            if (task.taskStatus === "FINISHED") {
                                // 
                                clearInterval(this.interval);
                            }

                            this.updateState(task);
                        });
                }, 5000);
            }
        }
    }

    startAsr() {
        axios.get(this.host + '/asr/StartTask?taskId=' + this.taskId)
            .then(resp => {
                let task = resp.data.data;

                this.updateState(task);
            });
    }

    render() {

        return (
            <div className="container py-4">
                <Header/>

                <div className="card">
                    <div className="card-header">
                        <div className="row">
                            <div className="col p-2">
                                <i className="bi bi-file-earmark-play"></i>{this.state.fileName}
                            </div>
                            <div className="col-md-3 text-center">
                                <button type="button"
                                        className={" btn btn-primary " + (this.state.taskStatus === 'READY' ? "" : "disabled")}
                                        onClick={this.startAsr}
                                >{this.status2ActionName(this.state.taskStatus)}</button>

                                <a href={this.host + "/asr/DownloadSrtFile?taskId=" + this.taskId}
                                   className={"float-end btn btn-success " + (this.state.taskStatus === "SUCCESS" ? "" : "disabled")}
                                   onClick={this.startAsr}
                                >SRT
                                </a>
                            </div>
                        </div>


                    </div>

                    <div className="card-body">
                        <div className="progress" role="progressbar" aria-label="Animated striped example"
                             aria-valuenow="75" aria-valuemin="0" aria-valuemax="100">
                            <div className="progress-bar progress-bar-striped progress-bar-animated"
                                 style={{"width": this.state.taskProgress + "%"}}
                            ></div>

                        </div>
                    </div>

                    <div className="card-footer">
                        <div dangerouslySetInnerHTML={{__html: this.state.resultText.replaceAll("\n", "<br />")}}></div>
                    </div>
                </div>
                <Footer/>
            </div>
        );
    }
}


export default Subtitle;