import './App.css';
import React from "react";
import useState from 'react';
import axios from "axios";
import $ from 'jquery';
import Header from "./Header";
import Footer from "./Footer";

class App extends React.Component {


    constructor(props) {
        super(props);
        this.state = {
            progress: 0
        }
        this.startTimer = this.startTimer.bind(this);
    }

    uploaded(event, data) {
        alert('abc' + data.response.data.taskId);
    }

    startTimer() {
        var cnt = 0;
        let maxTry = 600;
        var progress = 0;

        this.setState({progress: progress + 1})

        let interval = setInterval(() => {
            console.log("get task progress");
            cnt += 1;
            if (cnt > maxTry) {
                clearInterval(interval);
            }

            this.setState({progress: progress + 1});
            progress += 1;

            // axios.get('http://localhost:9000/asr/GetTaskById?taskId=c770f8e7-138a-4fd1-9940-46f1f6e1b6df')
            //     .then(resp => {
            //         console.log(resp.data);
            //         this.setState({progress: resp.data.data.taskProgress});
            //         if (resp.data.data.taskProgress === 100) {
            //             clearInterval(interval);
            //         }
            //     });
        }, 5000);
    }

    componentDidMount() {
        $(this.fileinput)
            // .fileinput()
            .on('fileuploaded', this.uploaded);
    }


    render() {
        return (
            <div className="container py-4">
                <Header />


                <div className="p-2 mb-4 bg-light rounded-3">
                    <div className="container-fluid py-2">
                        <input ref={elem => this.fileinput = elem} id="input-id"
                               type="file"
                               className="file"
                               data-preview-file-type="text"
                               data-language="zh"
                               data-upload-url="http://localhost:9000/asr/uploadFile"
                        />
                    </div>
                </div>

                <div className="row align-items-md-stretch">
                    <div className="row py-3">
                        <div className="col-md-1">
                            <span className="badge text-bg-primary">AI</span>
                        </div>
                        <div className="col-md-11">
                            <div className="progress" role="progressbar" aria-label="Danger striped example"
                                 aria-valuenow="100" aria-valuemin="0" aria-valuemax="100">
                                <div className="progress-bar progress-bar-striped bg-success"
                                     style={{width: this.state.progress + "%"}}></div>
                            </div>
                        </div>
                    </div>

                    <div className="col-md-12">
                        <div className="h-100 p-5 text-bg-dark rounded-3">
                            <button className="btn btn-outline-light" type="button"
                                    onClick={this.startTimer}>Download srt file
                            </button>

                            <h2>Change the background</h2>
                            <p>Swap the background-color utility and add a `.text-*` color utility to mix up the
                                jumbotron
                                look. Then, mix and match with additional component themes and more.</p>
                        </div>
                    </div>

                </div>

                <Footer />
            </div>
        );
    }
}

export default App;
