import React from "react";
import $ from 'jquery';
import Header from "./Header";
import Footer from "./Footer";

class Uploader extends React.Component {
    loaded = false;
    host = window.location.host.indexOf('localhost') >= 0 ? "http://localhost:9000" : "http://asr.flamingo-app.cn";

    componentDidMount() {
        if (this.loaded) {
            return;
        }
        this.loaded = true;

        $(this.fileinput)
            .fileinput()
            .on('fileuploaded', function (event, data) {

                // 
                $('#btnModal').click();

                let resp = data.response
                if (resp && resp.success) {
                    let taskId = data.response.data.taskId;

                    setTimeout(() => {
                        window.location = '/subtitle/' + taskId;
                    }, 2000)
                }
            });
    }

    render() {
        return (
            <div className="container py-4">
                <Header/>
                <input id="input-id" type="file" className="file"
                       ref={elem => this.fileinput = elem}
                       data-preview-file-type="text"
                       data-language="zh"
                       data-upload-url={this.host + "/asr/UploadFile"}
                />

                <button type="button" className="btn btn-primary"
                        id='btnModal'
                        data-bs-toggle="modal"
                        data-bs-target="#redirectModal"
                        style={{"display":"none"}}
                >
                    text
                </button>

                <div className="modal fade" id="redirectModal" tabIndex="-1"
                     aria-labelledby="exampleModalLabel"
                >
                    <div className="modal-dialog modal-dialog-centered">
                        <div className="modal-content">
                            <div className="modal-header">
                                <h1 className="modal-title fs-5" id="exampleModalLabel"></h1>
                                <button type="button" className="btn-close" data-bs-dismiss="modal"
                                        aria-label="Close"></button>
                            </div>
                            <div className="modal-body">
                                ...
                            </div>
                        </div>
                    </div>
                </div>

                <Footer/>
            </div>
        );
    }
}

export default Uploader;